<template>
  <header class="main-header">
    <a href="/" class="logo">cms</a>
    <nav class="navbar navbar-static-top">
      <div class="navbar-custom-menu">
        <ul class="nav navbar-nav">
          <li class="dropdown notifications-menu">
            <a href="#" class="dropdown-toggle" data-toggle="dropdown">
              <span class="hidden-xs" id="adminUserName">{{name}}</span>
            </a>
          </li>
          <li class="dropdown user user-menu">
            <a href="javascript:;" class="dropdown-toggle" @click="logout">
              <span class="hidden-xs" id="adminUserName"></span>
            </a>
          </li>
        </ul>
      </div>
    </nav>
  </header>
  <div class="main-sidebar">
    <div class="sidebar">
      <ul class="sidebar-menu">
        <li class="header"></li>
        <li class="treeview">
          <a></a>
          <ul class="treeview-menu" style="display:block;">
            <li v-link="{ path: '/product', activeClass: 'active' }"><a v-link="{ path: '/product', activeClass: 'active' }"></a></li>
            <li v-link="{ path: '/sort', activeClass: 'active'}"><a v-link="{ path: '/sort', activeClass: 'active'}"></a></li>
            <li v-link="{ path: '/ad', activeClass: 'active' }"><a v-link="{ path: '/ad', activeClass: 'active' }"></a></li>
            <!-- <li v-link="{ path: '/nav', activeClass: 'active' }"><a v-link="{ path: '/nav', activeClass: 'active' }"></a></li> -->
          </ul>
        </li>
        <li class="treeview">
          <a></a>
          <ul class="treeview-menu" style="display:block;">
            <li v-link="{ path: '/home', activeClass: 'active' }"><a v-link="{ path: '/home', activeClass: 'active' }"></a></li>
            <li v-link="{ path: '/sample', activeClass: 'active' }"><a v-link="{ path: '/sample' }"></a></li>
            <li v-link="{ path: '/industrial', activeClass: 'active' }"><a v-link="{ path: '/industrial', activeClass: 'active' }"></a></li>
            <li v-link="{ path: '/market', activeClass: 'active' }"><a v-link="{ path: '/market', activeClass: 'active' }"></a></li>
            <!-- <li v-link="{ path: '/d', activeClass: 'active'}"><a v-link="{ path: '/d', activeClass: 'active'}"></a></li> -->
          </ul>
        </li>
        <li class="treeview">
          <a></a>
          <ul class="treeview-menu" style="display:block;">
            <li v-link="{ path: '/user', activeClass: 'active'}"><a v-link="{ path: '/user'}"></a></li>
            <li v-link="{ path: '/views', activeClass: 'active'}"><a v-link="{ path: '/views'}"></a></li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  module.exports = {
    data: function() {
      return {
        name: localStorage.name
      }
    },
    methods: {
      logout: function() {

        localStorage.removeItem('name');
        $.ajax({
          url: domain + '/logout',
          type: 'POST',
          data: {name: localStorage.name},
        });

        this.$route.router.go('/login');
      }
    }
  }

</script>



